@charset "UTF-8";
.jonnitto-prettyembedbackend__label {
  position: absolute;
  display: block;
  background: #00adee;
  color: #fff;
  padding: 5px 15px;
  cursor: pointer;
  transform: translateY(-100%);
  z-index: 1;
}

.jonnitto-prettyembedbackend__label .jonnitto-prettyembedbackend__hide {
  display: none;
}

.jonnitto-prettyembedbackend__checkbox {
  display: none;
}

.jonnitto-prettyembedbackend__checkbox:checked ~ .jonnitto-prettyembedbackend__label .jonnitto-prettyembedbackend__show {
  display: none;
}

.jonnitto-prettyembedbackend__checkbox:checked ~ .jonnitto-prettyembedbackend__label .jonnitto-prettyembedbackend__hide {
  display: block;
}

.jonnitto-prettyembedbackend__checkbox:checked ~ .jonnitto-prettyembedbackend__table {
  display: table;
}

.jonnitto-prettyembedbackend__table {
  display: none;
  width: 100%;
  text-align: left;
  margin: 1em 0 2em;
  position: relative;
}

.jonnitto-prettyembedbackend__table th:not(:first-child),
.jonnitto-prettyembedbackend__table td:not(:first-child) {
  padding-left: 5px;
}

.jonnitto-prettyembedbackend__table th:not(:last-child),
.jonnitto-prettyembedbackend__table td:not(:last-child) {
  text-align: inherit;
  padding-right: 5px;
}

.jonnitto-prettyembedbackend__table th.jonnitto-prettyembedbackend__center, .jonnitto-prettyembedbackend__table th:last-child,
.jonnitto-prettyembedbackend__table td.jonnitto-prettyembedbackend__center,
.jonnitto-prettyembedbackend__table td:last-child {
  text-align: center;
}

.jonnitto-prettyembedbackend__table .jonnitto-prettyembedbackend__error {
  background: #d9534f;
  color: #fff;
  padding: 0 !important;
}

.jonnitto-prettyembedbackend__table tbody td {
  border-top: 1px solid #aaa;
}

.jonnitto-prettyembedbackend__table tbody > :not(td) {
  display: table-row;
}

.jonnitto-prettyembedbackend__table tbody > [data-__neos-fusion-path*="removedContent"]:first-child,
.jonnitto-prettyembedbackend__table tbody > [class^="style__addEmptyContentCollectionOverlay"] {
  outline: 0;
}

.jonnitto-prettyembedbackend__table tbody:empty,
.jonnitto-prettyembedbackend__table tbody > [data-__neos-fusion-path*="removedContent"]:first-child,
.jonnitto-prettyembedbackend__table tbody > [class^="style__addEmptyContentCollectionOverlay"] {
  cursor: pointer;
  height: 41px;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.jonnitto-prettyembedbackend__table tbody:empty::before,
.jonnitto-prettyembedbackend__table tbody > [data-__neos-fusion-path*="removedContent"]:first-child::before,
.jonnitto-prettyembedbackend__table tbody > [class^="style__addEmptyContentCollectionOverlay"]::before {
  position: relative;
  display: block;
  opacity: 0.95;
  transition: opacity 0.2s ease;
  line-height: 41px;
  color: #fff;
  content: "Click here to add captions";
  z-index: 1;
}

.jonnitto-prettyembedbackend__table tbody:empty::before [lang^="de"],
.jonnitto-prettyembedbackend__table tbody > [data-__neos-fusion-path*="removedContent"]:first-child::before [lang^="de"],
.jonnitto-prettyembedbackend__table tbody > [class^="style__addEmptyContentCollectionOverlay"]::before [lang^="de"] {
  content: "Hier klicken um Inhalt hinzuzufügen";
}

.jonnitto-prettyembedbackend__table tbody:empty::after,
.jonnitto-prettyembedbackend__table tbody > [data-__neos-fusion-path*="removedContent"]:first-child::after,
.jonnitto-prettyembedbackend__table tbody > [class^="style__addEmptyContentCollectionOverlay"]::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 41px;
  background: #00adee;
  opacity: 0.95;
  z-index: 0;
  margin: 0;
  animation: none;
}

.jonnitto-prettyembedbackend__table tbody:empty:hover::before, .jonnitto-prettyembedbackend__table tbody:empty:hover::after,
.jonnitto-prettyembedbackend__table tbody > [data-__neos-fusion-path*="removedContent"]:first-child:hover::before,
.jonnitto-prettyembedbackend__table tbody > [data-__neos-fusion-path*="removedContent"]:first-child:hover::after,
.jonnitto-prettyembedbackend__table tbody > [class^="style__addEmptyContentCollectionOverlay"]:hover::before,
.jonnitto-prettyembedbackend__table tbody > [class^="style__addEmptyContentCollectionOverlay"]:hover::after {
  opacity: 1;
}

.jonnitto-prettyembedbackend__table tbody > [data-__neos-fusion-path*="removedContent"]:first-child {
  box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.3);
}
